
import { Component, OnInit } from '@angular/core';
import { CORE_DIRECTIVES, FORM_DIRECTIVES } from '@angular/common';

import {HeaderComponent} from '../common/header/';
import {FooterComponent} from '../common/footer/';
import { DispserviceService } from '../services/dispservice.service';
import {Dispatches} from '../services/dispatch'
import {DispatchInput} from '../services/dispatch'
import {AddstoreComponent} from '../common/addstore/'





@Component({
  moduleId: module.id,
  selector: 'app-merc-main',
  templateUrl: 'merc-main.component.html',
  styleUrls: ['merc-main.component.css'],
  directives: [CORE_DIRECTIVES, FORM_DIRECTIVES, HeaderComponent, FooterComponent, AddstoreComponent],
      providers: [DispserviceService]
  
})

export class MercMainComponent implements OnInit {
    private _webapi: string = 'http://merchandiser/api/Merc/GetDispatches/2016-06-11';
    public dispatch: Array<Dispatches>;
    public dispInput: DispatchInput;
    private x: string;


    constructor(
        public dispService: DispserviceService) {
    }

 

    ngOnInit() {
        this.dispService.set(this._webapi);
        this.dispInput = new DispatchInput('System',101, new Date('2016-06-11'));
        //this.getDipatchInfoPost(this.dispInput);
        this.getDipatchInfo();
    }

    getDipatchInfo(): void {
        this.dispService.get()
            .subscribe(res => {
                var data: any = res.json();
                this.dispatch = data.Routes;
              
            },
            error => {

                if (error.status == 401 || error.status == 404) {
                    //this.notificationService.printErrorMessage('Authentication required');
                    //this.utilityService.navigateToSignIn();
                }
            });
        
    }


    getDipatchInfoPost(data: DispatchInput) {
        this.dispService.post(JSON.stringify(data))
            .subscribe(res => {
                var data: any = res.json();
                this.dispatch = data;

            },
            error => {

                if (error.status == 401 || error.status == 404) {
                    //this.notificationService.printErrorMessage('Authentication required');
                    //this.utilityService.navigateToSignIn();
                }
            });
    }
}